import React from 'react';
import { Select, message } from 'antd';
import { Record } from './record.js'
import likesData from './like.json'
import './style.css';
const { Option } = Select;
const children = [];
const star = ['', '★', '★★', '★★★', '★★★★', '★★★★★']

let list = Object.keys(likesData)
for (let i = 0; i < list.length; i++) {
  let item = likesData[list[i]]
  children.push(<Option key={list[i]}>
    {item.name}
    {star[item.star]}
  </Option>);
}


class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      history: [{
        squares: Array(9).fill(null)
      }],
      labelll: '',
      select: {}
    }
  }
  render() {
    return (
      <div className="like"
        style={{ textAlign: 'left', padding: "0 20px" }}>
        {/* 好感度查询，如有问题可加群反馈：926315142<br /> */}
        <Record />
        <Select
          placeholder="输名字, 搜索暂不支持昵称qwq"
          showSearch
          style={{ width: '90%' }}
          onChange={(value) => this.handleChange(value)}
          onSearch={(value) => this.onSearch(value)}
          filterOption={(input, option) => this.handleSearch(input, option)}
        >
          {children}
        </Select>
        <br />
        <span>{this.state.labelll}</span>
      </div>
    );
  }
  handleSearch = (input, option) => {
    return option.children[0].toLowerCase().includes(input.toLowerCase())
  };
  handleChange = (value) => {
    this.setState({ select: value });
    let str_plus = [(<span><strong>-----❤喜爱-----</strong><br /></span>)]
    let str_sub = [(<span><strong>-----💢厌恶-----</strong><br /></span>)]
    likesData[value].likes.map(item => {
      if (item.indexOf('+') != -1) {
        str_plus.push(<div className='flex margin' style={{ color: '#ab001a' }}><span>{item}</span><span onClick={() => handleStar(item)}>⭐</span></div>)
      } else {
        str_sub.push(<div className='flex  margin' style={{ color: 'green' }}>{item}<span onClick={() => handleStar(item)}>⭐</span></div>)
      }
    })
    if (str_plus.length == 1) str_plus.push(<span>糟糕，它什么也不喜欢哦...<br /></span>)
    if (str_sub.length == 1) str_sub.push(<span>好耶，它什么也不讨厌哦...<br /></span>)
    this.setState({ labelll: str_plus.concat(str_sub) })

    // 希望可以搞个退出时再写入收藏 
    const handleStar = (item) => {
      let storage = likesData[value].name + ' ' + item;
      let data = JSON.parse(localStorage.getItem('qwqDetail')) || {};
      const id = value;
      if (!data[id]) {
        data[id] = [];
      }
      if (!data[id]?.includes(storage)) {
        data[id].push(storage);
        message.success("已收藏：" + storage);
      } else {
        message.info("已经收藏过了qwq");
      }
      localStorage.setItem("qwqDetail", JSON.stringify(data));
    };
  };
  onSearch = (value) => {
    // console.log(value)
  }
}

export default App;

