import React from 'react';
import { Collapse } from 'antd';
import { data } from './data.js'
import './style.css';
const { Panel } = Collapse;


const App = React.FC = () => (
    <Collapse accordion>
        {Object.keys(data).map((key, index) => {
            return (<Panel header={key} key={index}>
                <pre className='pre'>{data[key]}</pre>
            </Panel>)
        })}
    </Collapse>
);

export default App;