// 这里希望架构上 搞一个全局的通信捏 (啥时候取消掉这个)
// 响应式有待验证
import React, { useState } from 'react';
import { Button } from 'antd';
import words from './words.json'
export const Record = () => {
    let [showRecord, setRecord] = useState(false);
    let [detail, setDetail] = useState('');
    const showFav = () => {
        showRecord = setRecord(!showRecord);
        const data = JSON.parse(localStorage.getItem('qwqDetail'));
        if (!data) {
            setDetail(null)
        } else {
            setDetail(data);
        }
    }
    const deleteFav = () => {
        localStorage.removeItem('qwqDetail');
        setDetail(null);
    }
    return (<div style={{ paddingBottom: "20px" }}>
        <div className='flex margin'>
            <Button className='favBtn' onClick={() => showFav()}>⭐我的收藏(本地)</Button>
            {showRecord === true ? <Button onClick={() => deleteFav()}>清空收藏</Button> : <span />}
        </div>
        {showRecord === true ? (
            detail ? <div >
                {Object.keys(detail)?.map((key, index) => {
                    return <div key={index}>{
                        detail[key]?.map((item, subindex) => {
                            return <div key={subindex}>{item}</div>
                        })
                    }
                    </div>
                })}
            </div>
                : <div>{words.noDetail}</div>)
            : <div />
        }
    </div>)
};
