import logo from './logo.svg';
import './App.css';
import "antd/dist/antd.css";
import { Radio } from 'antd';
import Likes from './like/likes.js'
import { Prob } from './prob/index.js'
import Wiki from './wiki/index.js'
import { Component } from 'react';
export default class App extends Component {
  constructor(...args) {
    super(...args);
    this.state = {
      showingTab: 'like'
    };
  }
  showComponent = (v) => {
    let obj = {
      a: "like",
      b: "wiki",
      c: "prob2"
    }
    this.setState({
      showingTab: obj[v.target.value]
    });
    // if (v.target.value === 'a') {
    //   this.setState({
    //     showingTab: 'like'
    //   });
    // } else if (v.target.value === 'b') {
    //   this.setState({
    //     showingTab: 'wiki'
    //   })
    // } else if (v.target.value === 'c') {
    //   this.setState({
    //     showingTab: 'prob2'
    //   })
    // }
  }
  render() {
    return (
      <div className="App">
        <div className='header' style={{ textAlign: 'left', marginBottom: 10, }}>
          <Radio.Group defaultValue="a" buttonStyle="solid" onChange={value => this.showComponent(value)}>
            <Radio.Button value="a">魔物好感度</Radio.Button>
            <Radio.Button value="b">小百科</Radio.Button>
            <Radio.Button value="c">模拟抽卡</Radio.Button>
          </Radio.Group>
        </div>
        <div className='content'>
        {this.state.showingTab === 'like' ? <Likes />
          : this.state.showingTab === 'wiki' ? <Wiki />
          : this.state.showingTab === 'prob2' ? <Prob />
          : null}
        <span></span>
        </div>
        <div className="bottom-fixed">{`Power by 香菇猫(>^ω^<)喵`} </div></div>
    );
  }
}
